//imports utilizados por esta clase
import React from "react";
import Container from "@mui/material/Container";
import logo from "../owlogo.png";
import caraGato from "../CaradeGato.PNG";
import "./image.css";

//Funcin principal que muestra informacin sobre como pueden contactarnos
//proporcionando correos electrnicos y telfonos de la empresa.
export default function AcercaDe() {
  const EstiloLogo = {};
  const mision = { fontSize: "1.2rem" }; //formato del contenido
  return (
    <Container maxWidth="md">
      <br />
      <div>
        <h3 align="center">Contacto</h3>
        <br />
        <div style={EstiloLogo}>
          <p className="aligncenter">
            <img //formato de imagen de un gato
              alt={"Un gato."}
              src={caraGato}
              width="40%"
              position="center"
            />
          </p>
          <p style={mision}>
            <br />
            En este espacio encontrar los correos electrnicos para cualquier
            duda o sugerencia sobre la aplicacin, tambin puede contactarnos si
            necesita ayuda tcnica y de proyectos.
            <br />
            <br />
            <br />
            <h3>Contctanos en los siguientes correo:</h3>
            <br />
            <ol>
              <li>contacto.kittenbook@gmail.com</li>
              <li>programacion.kittenbook@gmail.com</li>
            </ol>
            <br />
            <br />
            <br />
            <h3>Contctanos en los siguientes telfonos:</h3>
            <br />
            <ol>
              <li>+(872) 1373963</li>
              <li>+(871) 4839795</li>
            </ol>
          </p>
          <br />
        </div>
        <div style={EstiloLogo}>
          <p className="aligncenter">
            <img //formato del logo de la empresa
              alt={"Logo de la empresa."}
              src={logo}
              width="20%"
            />
          </p>
        </div>
      </div>
    </Container>
  );
}
