// Imports utilizados por esta clase
import React from "react";
import Container from "@mui/material/Container";

// Funcin que exporta la clase
export default function Dashboard(props) {
  return (
    // Contenedor
    <Container maxWidth="md">
      <br />
      <br />
      {/* Mensaje de bienvenida al usuario */}
      <h2 style={{ textAlign: "center" }}>Bienvenid@, {props.username}!</h2>
      <br />
      {/* Un gif */}
      <img
        style={{
          display: "block",
          marginLeft: "auto",
          marginRight: "auto",
          width: "50%",
        }}
        alt={"Un gato."}
        src={"http://media1.giphy.com/media/c54YHGDH63jJC/giphy.gif"}
      />
      <br />
      <br />
      {/* Componentes de ayuda de cada seccin de la pagina */}
      <h4>Cmo usar esta plataforma?</h4>
      <p>
        Kittenbook es una plataforma destinada al estudiante universitario. Como
        tal, te ayuda a organizarte en tu da a da.
      </p>
      <ul type="square">
        <li>
          <b>Notas</b> te ayuda a tomar notas rpidas o recordatorios que no
          quieras olvidar.
        </li>
        <li>
          <b>Horario</b> te ayuda a administrar tu agenda diaria, llevando
          cuenta de tu carga acadmica.{" "}
          <i>
            Slo puedes aadir materias que ests cursando actualmente a tu
            horario.
          </i>
        </li>
        <li>
          <b>Tareas</b> te ayuda a administrar tus deberes o trabajos asignados.{" "}
          <i>
            Slo puedes aadir tareas a materias que ests cursando y que estn
            registradas en tu horario.
          </i>
        </li>
        <li>
          <b>Retcula</b> te ayuda a administrar tu historial acadmico,
          llevando cuenta de tu promedio y de los crditos que tengas
          completados.
        </li>
      </ul>
    </Container>
  );
}
