//<Resumen>
//Clase Precios en la cual se muestra informacin de los precios de la app
//Contiene la funcin tiers, Precios y la funcin principal en lo que se realiza lo mencionado
//</Resumen>
//<Para> En esta clase se encuentra informacin de los precios para adquirir
//la aplicacin kittenbook
//<Autor>Omegaware</Autor>
//<Fecha de creacin> 15 de octubre de 2021</Fecha de creacin>

//imports utilizados por esta clase
import * as React from "react";

import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardHeader from "@mui/material/CardHeader";
import CssBaseline from "@mui/material/CssBaseline";
import Grid from "@mui/material/Grid";
import StarIcon from "@mui/icons-material/StarBorder";
import Typography from "@mui/material/Typography";
import GlobalStyles from "@mui/material/GlobalStyles";
import Container from "@mui/material/Container";

//funcin para mostrar la informacin de cada paquete de precios
const tiers = [
  {
    //informacin del paquete gratis
    title: "Gatito negro",
    price: "0",
    description: [
      "Productividad bsica",
      "Caja de arena",
      "Leche diaria",
      "Estambre incluido",
    ],
    buttonText: "Elegir",
    buttonVariant: "outlined",
  },
  {
    //informacin del paquete normal
    title: "Gato negro",
    subheader: "Sugerido",
    price: "111",
    description: [
      "Productividad moderada",
      "Salidas permitidas",
      "Ratones incluidos",
      "rbol propio",
    ],
    buttonText: "Elegir",
    buttonVariant: "contained",
  },
  {
    //informacin del paquete especial con todas las funcionalidades
    title: "Pantera negra",
    price: "279",
    description: [
      "Productividad mxima",
      "Sardinas semanales",
      "Contacto con otras camadas",
      "Sabana propia",
    ],
    buttonText: "Contctanos",
    buttonVariant: "outlined",
  },
];

//funcin para mostrar la informacin estructurada por paquete de precios
function Precios() {
  return (
    <React.Fragment>
      <GlobalStyles //formato de estilo grobal para el contenido
        styles={{ ul: { margin: 0, padding: 0, listStyle: "none" } }}
      />
      <CssBaseline />
      {/* Espaciado entre contenido de paquetes */}
      <Container
        disableGutters
        maxWidth="sm"
        component="main"
        sx={{ pt: 8, pb: 6 }}
      >
        <Typography //formato para la tipografa de la palabra "Precios"
          component="h1"
          variant="h2"
          align="center"
          color="text.primary"
          gutterBottom
        >
          Precios
        </Typography>
        <Typography //formato para la tipografa de "Productividad. Aprendizaje. Planificacin."
          variant="h5"
          align="center"
          color="text.secondary"
          component="p"
        >
          Productividad. Aprendizaje. Planificacin.
        </Typography>
      </Container>
      {/* Espaciado entre contenido de paquetes */}
      <Container maxWidth="md" component="main">
        <Grid container spacing={5} alignItems="flex-end">
          {tiers.map((tier) => (
            // formato para el grid y card para el contenido de la informacin
            <Grid
              item
              key={tier.title}
              xs={12}
              sm={tier.title === "Enterprise" ? 12 : 6}
              md={4}
            >
              <Card>
                <CardHeader //formato para las tarjetas del encabezado
                  title={tier.title}
                  subheader={tier.subheader}
                  titleTypographyProps={{ align: "center" }}
                  action={tier.title === "Pro" ? <StarIcon /> : null}
                  subheaderTypographyProps={{
                    align: "center",
                  }}
                  sx={{
                    backgroundColor: (theme) =>
                      theme.palette.mode === "light"
                        ? theme.palette.grey[200]
                        : theme.palette.grey[700],
                  }}
                />
                <CardContent>
                  <Box //formato del Box
                    sx={{
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "baseline",
                      mb: 2,
                    }}
                  >
                    <Typography //formato de la tipografa
                      component="h2"
                      variant="h3"
                      color="text.primary"
                    >
                      ${tier.price}
                    </Typography>
                    <Typography variant="h6" color="text.secondary">
                      /mes
                    </Typography>
                  </Box>
                  <ul>
                    {tier.description.map((line) => (
                      <Typography //formato para la tipografia de la descripcin
                        component="li"
                        variant="subtitle1"
                        align="center"
                        key={line}
                      >
                        {line}
                      </Typography>
                    ))}
                  </ul>
                </CardContent>
                <CardActions>
                  <Button fullWidth variant={tier.buttonVariant}>
                    {tier.buttonText}
                  </Button>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Container>
    </React.Fragment>
  );
}

//funcin principal donde se hace la llamada de la funcin anterior y as mostrar
//la informacin de los precios de la aplicacin kittenbook
export default function Pricing() {
  return <Precios />;
}
