//imports utilizados por esta clase
import React from "react";
import logoKitten from "../bookish.png";
import "./image.css";
import Container from "@mui/material/Container";

//funcin principal para mostrar la informacin del proyecto kittenbook
export default function Producto() {
  const EstiloLogo = {};
  const producto = { fontSize: "1.2rem", alignText: "justify" }; //formato para el contenido
  return (
    <Container maxWidth="md">
      <div>
        <br />
        <h3 align="center">Qu es?</h3>

        <div style={EstiloLogo}>
          <p className="aligncenter">
            <img //formato para el logo del proyecto
              alt={"Un gato."}
              src={logoKitten}
              width="60%"
            />
          </p>
          <p style={producto}>
            Kittenbook es un software orientado a mejorar la organizacin de la
            vida estudiantil, es ideal para alumnos que se les dificulta
            organizar sus tiempos y responsabilidades acadmicas.
            <br />
            <br />
            En el mundo acadmico surge la necesidad de administrar informacin
            de corto plazo, tales como tareas pendientes, proyectos en progreso,
            compromisos, notas tomadas en clase, etc., as como tambin
            informacin a largo plazo, tal como historial de asignaturas,
            crditos a cumplir, aplicaciones a residencias profesionales, etc.
            El lidiar de manera efectiva y organizada con la abundante
            informacin que surja es nuestro objetivo.
            <br />
            <br />
            Kittenbook resolver la problemtica de los estudiantes para
            organizar sus actividades escolares, (apuntes, horarios, retcula,
            etc), ya que esto les provoca que tengan problemas a la hora de
            buscar estos documentos.
            <br />
          </p>
          <br />
        </div>
        <br />
        <div style={EstiloLogo}>
          <p style={producto}>
            <h5>Novedades que ofrecemos</h5>
            <h6>Retcula</h6>
            Visualiza el estado de tus materias de una manera ms rpida y
            fcil.
            <br />
            <br />
            <br />
            <h6>Horario</h6>
            Organiza tus da, creando tu propio horario de materias.
            <br />
            <br />
            <br />
            <h6>Lista de Tareas</h6>
            Crea tu propia lista de tareas y consulta su estado.
            <br />
            <br />
            <br />
            <h6>Apuntes</h6>
            Necesitas guardar informacin importante? Aqu podrs crear notas y
            organizarlas con etiquetas.
            <br />
            <br />
            <br />
            <h6>Conteo de crditos</h6>
            Visualiza los crditos que llevas realizando durante el semestre en
            que te encuentres.
            <br />
            <br />
            <br />
          </p>
          <h2 align="center">Tu vida estudiantil al alcance de tu mano!</h2>
        </div>
      </div>
    </Container>
  );
}
